import React from 'react'

const char2 = () => {
    return (
        <>
            <div class="char2">
              <img  className="char-pic"src="./images/c2.jpg"/>
              <div class="info">
              <h1> Chris Evans </h1>
              <p>  Actor | </p>
              <h3> as Steve Rogers </h3>
              <p>   <strong> Born:</strong> June 13, 1981 in Boston, Massachusetts, USA </p>
               <p>  <strong> Birth Name:</strong> 	Christopher Robert Evans </p>
                 <p> <strong> Nickname:</strong> 	Cevans </p>
                 <p> <strong> Height: </strong> 	6' (1.83 m)</p>
                 </div>
                 </div> 
                 <div class="pass">
                <p> Christopher Robert Evans began his acting career in typical fashion: performing in school productions and community theatre. </p>
               <p> He was born in Boston, Massachusetts, the son of Lisa (Capuano), who worked at the Concord Youth Theatre, and G. Robert Evans III, a dentist. His uncle is former U.S. Representative Mike Capuano. Chris's father is of half German and half Welsh/English/Scottish ancestry, while Chris's mother is of half Italian and half Irish descent. He has an older sister, Carly Evans, and two younger siblings, a brother named Scott Evans, who is also an actor, and a sister named Shana Evans. The family moved to suburban Sudbury when he was 11 years-old. Bitten by the acting bug in the first grade because his older sister, Carly, started performing, Evans followed suit and began appearing in school plays. While at Lincoln-Sudbury Regional High School, his drama teacher cited his performance as "Leontes" in "The Winter's Tale" as exemplary of his skill. After more plays and regional theatre, he moved to New York and attended the Lee Strasberg Theatre Institute. </p>
               <p> On the advice of friends, he landed an internship at a casting office and befriended a couple of the agents he regularly communicated with - one of whom later took him on as a client. The screen - not the stage - then became his focus; Evans soon began auditioning for feature films and television series. Evans made one of his first appearances on The Fugitive (2000) (CBS, 2000-2001), a remake of the 1960s series and feature film starring Harrison Ford. In the episode "Guilt", Evans played the son of a small-town sheriff who tries to exact revenge after Dr. Richard Kimble - incognito as a liquor store owner - refuses to sell him and his friends alcohol. After small roles in Cherry Falls (2000) and The Newcomers (2000) - two unknown low-budget features - Evans appeared in Boston Public (2000) (Fox, 2000-2004) as a murder suspect. He then appeared in his first major feature, Not Another Teen Movie (2001), a spoof on teen comedies wherein he played a jock who makes a bet that he can turn an unpopular and unkempt girl (Chyler Leigh) into prom queen.</p>
               <p>  The year 2007 also proved to be one successful year for Chris, as he had two movies released around the world that same year, starting with the second installment of the Marvel franchise Fantastic Four. Chris received positive reviews for his performance. The Nanny Diaries (2007), where Evans played Harvard Hottie, showed his sensitive. The year 2008 saw Chris Evans' part of the movie Street Kings (2008), playing the character Detective Paul Diskant. The movie is about police officers trying to cover up their wrongdoings and audiences got to see a serious side of Chris. In the same year, Chris also worked on the movie The Loss of a Teardrop Diamond (2008).</p>
             
        </div>   
        </>
    )
}

export default char2
